
import { defineComponent, onBeforeUnmount, onMounted, ref } from 'vue'
import {
    IonPage,
    IonHeader,
    IonToolbar,
    IonButtons,
    IonContent,
    IonList,
    IonItem,
    loadingController,
    alertController,
    IonLabel,
    IonThumbnail,
    IonTitle,
    IonBackButton
} from '@ionic/vue';
import NeopleApi from '@/utils/NeopleApi';
import { useRoute } from 'vue-router';
import HomeButtonVue from '@/components/HomeButton.vue';
import WebShareButtonVue from '@/components/WebShareButton.vue';

export default defineComponent({
    components: {
        IonPage,
        IonHeader,
        IonToolbar,
        IonButtons,
        IonContent,
        IonList,
        IonItem,
        IonLabel,
        IonThumbnail,
        IonTitle,
        IonBackButton,
        HomeButtonVue,
        WebShareButtonVue
    },
    setup() {
        const abortController = new AbortController();
        const route = useRoute();
        const attributeId = route.params.attributeId as string;

        const positionInfo = ref();

        const errorHanbler = async (err: Error) => {
            if (err.name !== 'AbortError') {
                const alert = await alertController.create({
                    header: '오류 발생',
                    subHeader: `${err.message}`,
                    buttons: ['ok'],
                    mode: 'ios'
                })

                await alert.present();
            }
        }

        onMounted(async () => {
            const loading = await loadingController.create({
                message: "데이터 조회중",
                mode: "ios",
            });

            await loading.present();

            try {
                positionInfo.value = await NeopleApi.cyPositionInfo({ attributeId: attributeId }, { signal: abortController.signal });
            } catch (err: any) {
                errorHanbler(err);
            } finally {
                await loading.dismiss();
            }
        })

        onBeforeUnmount(() => {
            abortController.abort();
        })

        return {
            positionInfo,
            NeopleApi,
        }
    },
})
